























import { AppService } from "@/service/app.service";
import Vue from "vue";
import Component from "vue-class-component";

@Component({})
export default class CafeCreate extends Vue {
  app = AppService.instance;

  form = {
    countryCode: "",
    domain: "",
    rootDomain: "",
  };

  mounted(): void {
    this.app.api.countryAll();
  }

  async onSubmit(): Promise<void> {
    this.form.rootDomain = this.app.rootDomain;
    // console.log(this.form);
    try {
      const cafe = await this.app.api.cafeCreate(this.form);
      this.app.openCafe(cafe.id);
    } catch (e) {
      this.app.error(e);
    }
  }
}
